@import "../node_modules/bootstrap/scss/mixins";
@import "../node_modules/bootstrap/scss/functions";
@import "../node_modules/bootstrap/scss/variables";

@import "../node_modules/bootstrap/scss/reboot";
@import "../node_modules/bootstrap/scss/grid";
@import "../node_modules/bootstrap/scss/buttons";
@import "../node_modules/bootstrap/scss/card";
@import "../node_modules/bootstrap/scss/forms";
@import "../node_modules/bootstrap/scss/modal";
@import "../node_modules/bootstrap/scss/nav";
@import "../node_modules/bootstrap/scss/dropdown";
@import "../node_modules/bootstrap/scss/pagination";

body {
  background-color: rgba(221, 219, 219, 0.5);
  /* modal bug */
  padding: 0px !important;
}

nav {
  width: 100%;
  display: flex;
  flex-flow: row;
  align-items: center;
  flex-wrap: nowrap !important;
  justify-content: space-between !important;
  padding: 0.25rem 2rem;
  margin-bottom: 1rem;
  background-color: white !important;
  #logo {
    font-family: "Dancing Script", cursive;
    color: #000;
    font-size: 2rem;
    padding: 0px;
    text-decoration: none;
    &:hover {
      color: #000;
    }
  }
  .form-inline {
    min-width: 180px;
    flex-grow: 1;
    max-width: 250px;
    flex-wrap: nowrap !important;
    .error {
      box-shadow: #dc3545 0px 0px 5px 1px !important;
    }
    #searchInput {
      flex-shrink: 2;
    }
  }
  #login {
    font-size: 1.2rem;
    &:hover {
      color: #007bff;
    }
  }
}
.nav-item {
  display: flex;
  flex-flow: column;
  .dropdown-menu {
    text-align: right !important;
    position: absolute !important;
    right: 0px;
    left: auto !important;
    width: 35%;
    align-self: flex-end;
    justify-content: flex-end;
  }
}

/* pagination */

.navigation {
  width: 100%;
  display: flex;
  margin: 0.5rem 0px 2rem;
  .pagination {
    margin: auto;
  }
}

/* footer */

footer {
  position: fixed;
  width: 100%;
  bottom: 0px;
  padding: 0.5rem;
  text-align: center;
  background-color: black;
  color: white;
  z-index: 5;
  p {
    margin: 0.3rem;
    font-weight: 600;
  }
}

/* new recipe */

#recipeName {
  display: block;
  width: 100%;
  height: 3rem;
  font-size: 30px;
  font-weight: 600;
  border: 0px;
  border-bottom: 1px rgb(118, 118, 118) solid;
}
#recipeName:focus-visible,
#description:focus-visible {
  outline: 0px !important;
}
#description {
  display: block;
  width: 100%;
  border: 0px;
  resize: none;
}
#description::placeholder {
  font-size: 1.2rem;
}
#top-div {
  width: 100%;
  padding: 3px;
}
#ingredients-container,
#steps-container {
  margin: 1rem;
  padding: 0.5rem 1rem;
}
.step-div {
  padding: 0.3rem;
}
.ingredient-container,
.step-container {
  display: flex;
  margin: 1rem 0px;
  justify-content: center;
  align-items: center;
}
.ingredient {
  width: 100%;
}
.ingredient-name,
.quantity {
  width: 50%;
  border: 1px rgb(118, 118, 118) solid;
}
.ingredient-name {
  border-right: 0px;
  border-radius: 3px 0 0 3px;
}
.quantity {
  border-radius: 0 3px 3px 0;
}
.add-substract {
  margin-left: 0.5rem;
}
.add-ingredient,
.delete-ingredient,
.add-step,
.delete-step {
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 120%;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 0.7rem;
  font-weight: 900;
  margin-left: 0.3rem;
  flex-shrink: 0;
}
.step-label,
.step {
  margin: 0px;
  padding: 0px;
}
.step-label {
  display: inline-block;
  width: initial;
  margin-right: 0.2rem;
  flex-shrink: 0;
}
.step {
  flex-grow: 2;
  flex-shrink: 1;
  border-radius: 3px;
  border: 1px rgb(118, 118, 118) solid;
}
#form-bottom {
  display: flex;
  justify-content: space-around;
  padding: 1rem 0rem 2rem;
}

#frontImage {
  display: none;
}
#file-button,
#form-button {
  background-color: white;
  width: 40%;
  height: 3rem;
  text-align: center;
  padding: 0.5rem;
  border: 2px #afbbc7 outset;
  &:active {
    border: 2px #afbbc7 inset;
  }
}
#text-limit {
  position: relative;
  text-align: right;
  right: 0.3rem;
  top: 0px;
  opacity: 40%;
}
#preview {
  max-width: 100%;
  border-radius: 8px 0 0 8px;
  width: 0px;
}
#top-form {
  background-color: white;
  height: 200px;
  display: flex;
  border: 1px #afbbc7 solid;
  border-radius: 8px;
}
.width {
  width: 45% !important;
  object-fit: cover;
}

/* card css */

.card {
  width: 16rem;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  margin: 0.5rem auto;
  margin-bottom: 2rem;
  transition: 0.25s;
  border-radius: 6px !important;

  &:hover {
    transform: translate(-5px, -10px);
    box-shadow: black 4px 4px 6px;
  }
  .card-img-top {
    height: 250px;
    object-fit: cover;
  }
  .card-body {
    min-height: 10rem;
    padding: 1rem 1rem 2rem 1rem !important;

    .card-title {
      font-size: 1.5rem;
    }
    .card-author {
      text-align: right;
      margin: 0px;
      color: dimgray;
      position: absolute;
      bottom: 1rem;
      right: 1rem;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

/* modal */

.register-modal-footer {
  margin-top: 1rem;
}
label {
  padding: 1rem;
  width: 100%;
}
#register {
  padding: 1rem;
}
#error-message {
  border-radius: 4px;
  width: 100%;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  color: #282a35;
  padding: 15px;
  position: relative;
  box-sizing: border-box;
  background-color: #ffc0c7;
  display: none;
}
.display {
  display: block !important;
}

/* individual recipe */

#top-secction {
  background-color: white;
}
.recipe-pag {
  flex-direction: column;
  justify-content: left;
}
#name-description-container {
  margin-top: 1.5rem;
}
#name-description-container h4 {
  margin-top: 1rem;
}
#ingredients-container,
#steps-container {
  flex-grow: 1;
  border: 1px #afbbc7 solid;
  border-radius: 10px;
  background-color: white;
}
#ingredients-container ul {
  margin: 0.5rem;
  padding-left: 1.5rem;
}
#ingredients-container h4 {
  margin: 0.3rem 0.5rem 1rem;
}
#ingredients-container ul li {
  margin: 0.3rem;
}
.step-span {
  margin-right: 5px;
}
.author-recipes-container {
  margin-top: 2rem;
}
.author-recipes {
  display: flex;
  flex-wrap: wrap;
}

/* container */

.container {
  max-width: 60vw !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 2rem;
}

@media only screen and (max-width: 899px) {
  footer * {
    margin: 0.2rem !important;
    font-size: 1rem;
  }
  .card {
    width: 100%;
    margin-bottom: 2rem !important;
  }

  .container {
    padding-bottom: 0.5rem !important;
  }
  .recipe-image {
    object-fit: cover;
    padding: 0rem;
    max-height: 400px;
    border-radius: 8px 8px 0 0;
    width: 100%;
  }
  #ingredients-container,
  #steps-container {
    margin: 1rem 0px;
  }
  #top-secction {
    border-radius: 8px;
    border: #afbbc7 1px solid;
  }
  h2 {
    padding-bottom: 0.5rem;
  }
  #name-description-container {
    padding: 0 0 1rem 1rem;
  }
  #login {
    padding-left: 1rem;
  }
  .card-img-top {
    height: auto !important;
    max-height: 400px;
    object-fit: cover;
  }
  .form-control {
    min-width: 100px;
  }
  .author-recipes {
    padding-bottom: 3rem;
  }
}
@media only screen and (min-width: 900px) {
  .recipe-pag {
    width: 60% !important;
  }
  .recipe-image {
    width: 100%;
    max-width: 50%;
    height: 300px;
    border-radius: 8px 0 0 8px;
    object-fit: cover;
  }
  #top-secction {
    display: flex;
    border: 1px #afbbc7 solid;
    border-radius: 8px 8px 8px 8px;
    margin: 1rem;
  }
  #name-description-container {
    margin: 1rem;
  }
  #form-new-recipe {
    width: 60%;
  }
  #new-recipe-form {
    max-width: 50%;
    width: 50%;
  }
  #top-form {
    margin: 1rem;
  }
  .author-recipes-container h3 {
    margin-left: 1rem;
  }
}
